.study-page-container {
    width: 50em;
    max-width: 800px !important;
    padding: 20px;
    margin: auto;
}

.study-page-header{
    height: 200px;
    width: 100%;
    background-color: #eee;
    justify-content: center;
    padding: 20px;
    text-align: center;
    vertical-align: middle;
    display:flex;
    flex-direction:column;
    box-shadow: inset 0px -5px 10px -7px #666;
}

.back-button {
    position:fixed;
    opacity: 1;
    color: #ffcd71;
}

.back-button:hover {
    color: #ffb52b !important;
}

@media (max-width: 769px) {
    .study-page-container {
        width: 100%;
    }

    .back-button {
        display: none !important;
    }

    .study-page-description {
        flex-direction: column !important;
    }

    .study-page-value {
        flex: 0 1 100%;
        display: flex;
        flex-direction: column;
    }
}

.study-page-container {
    display: flex;
    flex-direction: column;
}

.study-page-image {
    display:flex;
}

.study-page-img {
    width: 300px;
    margin:auto;
    object-fit: cover;
}

.study-page-description {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.study-page-textbox {
    flex: 0 1 100%;
}

.study-page-value {
    display: flex;
    flex-direction: column;
}

.study-page-statistics {
    width: 100%;
    display:flex;
    flex-direction: row;
}

.statistic-item {
    flex: 0 1 50%;
    margin:auto;
    display: flex;
    flex-direction: column;
    justify-items: center;
    vertical-align: middle;
}

.task-checkbox {
    margin: auto;
    margin-left: 10px;
    margin-top: 20px;
}

.additionalResources {
    margin-top: 10px;
    margin-left: 7px;
    display: flex;
    flex-direction: column;
}

.ant-checkbox-wrapper {
    display: flex !important;
}

#root > div > div.study-page-container > div.study-page-description > div.study-page-value > div > label > span:nth-child(2) > h3 {
    margin-top: -5px !important;
}

#root > div > div.study-page-container > div.study-page-description > div.study-page-value > div:nth-child(5) > div > label > span:nth-child(2) > h3 {
    margin-top: -5px !important;
}

.subResources {
    margin-top: 5px;
}