body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    background: #eee;
    font-family: sans-serif;
}

/* Result Component */
.result {
    padding: 1.5rem 2.5rem;
}

/* Quiz Component */
.answerOptions {
    margin: 0;
    padding: 0;
    list-style: none;
}

/* QuestionCount Component */
.questionCount {
    padding: 1.5rem 2.5rem 0;
    font-size: 14px;
}

/* Question Component */
.question {
    margin: 0;
    padding: 0.5rem 2.5rem 1.5rem 2.5rem;
}

/* AnswerOption Component */
.answerOption {
    border-top: 1px solid #eee;
}

.answerOption:hover {
    background-color: #eefbfe;
}

.radioCustomButton {
    position: absolute;
    width: auto;
    opacity: 0;
}

.radioCustomButton,
.radioCustomLabel {
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
}

.radioCustomLabel {
    position: relative;
    width: 100%;
    margin: 0;
    padding: 1.5rem 2.5rem 1.5rem 6rem;
    font-size: 16px;
    line-height: 1.5;
}

.radioCustomButton~.radioCustomLabel:before {
    position: absolute;
    top: 20px;
    left: 38px;
    width: 28px;
    height: 28px;
    content: '';
    display: inline-block;
    vertical-align: middle;
    background: #fff;
    border: 1px solid #bbb;
    border-radius: 50%;
    transition: all 0.3s;
}

.radioCustomButton:checked~.radioCustomLabel:before {
    content: '';
    background: #8bc53f url(/icons/icon-check.svg) no-repeat;
    background-size: 27px;
    border-color: #8bc53f;
}

/* Animation */
.fade-enter {
    opacity: 0;
}

.fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 0.5s ease-in-out 0.3s;
}

.fade-leave {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    opacity: 1;
}

.fade-leave.fade-leave-active {
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
}

.fade-appear {
    opacity: 0;
}

.fade-appear.fade-appear-active {
    opacity: 1;
    transition: opacity 0.5s ease-in-out;
}

.actionButton {
    margin: auto;
    margin-top: 1em;
    float:right;
    margin-right: 1em;
    width: 100px;
    height: 50px !important;
    font-size: 15pt !important;
    transition: all .2s;
}

.progressContainer {
    width: 100% !important;
    display: flex;
    flex-direction: column;
    justify-items: center;
}

.questionProgress { 
    width: 90% !important;
    margin: auto !important;
}

.questionnaireContainer {
    display: flex;
    flex-direction: column;
    justify-items: center;
}

.questionnaire {
    width: 50em;
    max-width: 800px !important;
    padding: 20px;
    margin: auto;
}

@media (max-width: 769px) {
    .questionnaire {
        width: 100%;
    }
}

.questionnaireHeader{
    height: 200px;
    width: 100%;
    background-color: #eee;
    justify-content: center;
    padding: 20px;
    text-align: center;
    vertical-align: middle;
    display:flex;
    flex-direction:column;
    box-shadow: inset 0px -5px 10px -7px #666;
}

.questionnaireHeader h1 {
    margin: auto;
    margin-bottom: 10px;
}

.questionnaireHeader h3 {
    margin: auto;
    margin-top: 10px;
}