.studies-header{
    height: 200px;
    width: 100%;
    background-color: #eee;
    justify-content: center;
    padding: 20px;
    text-align: center;
    vertical-align: middle;
    display:flex;
    flex-direction:column;
    box-shadow: inset 0px -5px 10px -7px #666;
}

.study-item {
    padding: 20px !important;
    border-radius: 7px !important;
    transition: 0.3s all;
    border-bottom: 5px solid #ffa700 !important;
}

.study-item:hover {
    background-color: #eee !important;
    transition: 0.3s all;

}

.studies-container {
    width: 50em;
    max-width: 800px !important;
    padding: 20px;
    margin: auto;
}

@media (max-width: 769px) {
    .studies-container {
        width: 100%;
    }
}

.study-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.float-feedback {
    position:fixed;
    bottom: 1em;
    right: 1em;
    width: 4.5em;
    height: 4.5em;
    background-color: white;
    padding: 5px;
    box-shadow: 0px 0px 5px 0px #666;
    border-radius: 200px;
    display:flex;
    justify-items: center;
    vertical-align: center;
    transition: 0.3s all;
}

.float-feedback:hover {
    background-color: #fff8eb;
    box-shadow: 0px 0px 10px 0px #666; 
    transition: 0.1s all;
}

.float-feedback:active {
    background-color: #ffe5b4;
    box-shadow: 0px 0px 2px 0px #666; 
}

.float-feedback-logo {
    object-fit: contain;
    width: 3em;
    height: 3em;
    margin: auto;
}